        margin: auto auto 5%; } }
  .hero__slide {
    transition: all 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    opacity: 0;
    transform: translateX(-100%);
    position: absolute;
    width: 100%;
    height: 100%; }
    .hero__slide:last-child {
      transform: translateX(100%); }
    .hero__slide.js-active {
      opacity: 1;
      transform: translateX(0); }
  .hero__slide-block {
    position: absolute;
    width: 63.5%;
    height: 85%;
