  .hero__slider-button {
    margin-bottom: 2.5rem;
    min-width: 17.5rem;
    height: 4rem;
    max-width: 30rem;
    position: relative;
    justify-content: center;
    align-items: center;
    display: inline-flex;
    background: none;
    color: #fff;
    border: none;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    letter-spacing: .1rem;
    font-size: 1.2rem;
    margin-left: .5rem;
    margin-right: .5rem;
    /* Top and bottom borders */
    /* Left and right borders */
    /*Active state borders */ }
    @media (min-width: 768px) {
      .hero__slider-button {
        margin-bottom: 0; } }
    .hero__slider-button:before, .hero__slider-button:after {
      transition: all 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
      content: '';
      position: absolute;
      height: .2rem;
      background: #fff; }
    .hero__slider-button:before {
      top: 0;
      left: 100%;
      right: 0; }
    .hero__slider-button:after {
      bottom: 0;
      right: 100%;
      left: 0; }
    .hero__slider-button span {
      pointer-events: none; }
      .hero__slider-button span:before, .hero__slider-button span:after {
        transition: all 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
        content: '';
        position: absolute;
        width: .2rem;
        background: #fff; }
      .hero__slider-button span:before {
        top: 0;
        left: 0;
        bottom: 100%; }
      .hero__slider-button span:after {
        top: 100%;
        right: 0;
        bottom: 0; }
    .hero__slider-button.js-active:before {
      left: 0; }
    .hero__slider-button.js-active:after {
      right: 0; }
    .hero__slider-button.js-active span:before {
      bottom: 0; }
    .hero__slider-button.js-active span:after {
      top: 0; }
    .hero__slider-button ._hover {
      position: absolute;
      left: 0;
      right: 0;
      margin: auto;
      width: .2rem;
      height: 4rem;
      pointer-events: none; }
      .hero__slider-button ._hover:before, .hero__slider-button ._hover:after {
        transition: all 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
        content: '';
        height: 3rem;
        width: .2rem;
        background: #fff;
        display: block;
        position: absolute;
        top: .5rem;
        left: 0;
        right: 0;
        margin: auto;
        opacity: 0; }
      .hero__slider-button ._hover:before {
        transform: skewX(-30deg) translate3d(0, -150%, 0); }
      .hero__slider-button ._hover:after {
        transform: skewX(-30deg) translate3d(0, 150%, 0); }
    .hero__slider-button:hover ._hover:before {
      transform: skewX(-30deg) translate3d(0, -90%, 0);
      opacity: 1; }
    .hero__slider-button:hover ._hover:after {
      transform: skewX(-30deg) translate3d(0, 90%, 0);
      opacity: 1; }

/* Animations */
@keyframes skew {
  from {
    transform: skewX(0); }
  to {
    transform: skewX(-30deg);
    left: 53%; } }

@keyframes fadeInUp {
  from {
    transform: translateY(20%);
    opacity: 0; }
  to {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fadeInLeft {
  from {
    transform: translateX(-20%);
    opacity: 0; }
  to {
    transform: translateX(0);
    opacity: 1; } }

@keyframes fadeInRight {
  from {
    transform: translateX(20%);
    opacity: 0; }
  to {
    transform: translateX(0);
    opacity: 1; } }

